.timer {
  padding: 30px 200px;
}

.timer-form {
  justify-content: space-evenly;
  display: flex;
}

.container {
  text-align: center;
  flex-direction: column;
  display: flex;
}

.timer-input {
  border: 3px solid #000;
  border-radius: 20px;
  max-width: 250px;
  height: 200px;
  padding-left: 25px;
  font-size: 200px;
}

.timer-label {
  margin-top: 10px;
  font-size: 30px;
}

.buttons-container {
  grid-template-rows: repeat(3, 200px);
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin-top: 40px;
  display: grid;
}

.buttons-container > button {
  cursor: pointer;
  border: none;
  border-radius: 20px;
  font-family: Courier New, Courier, monospace;
  font-size: 80px;
  transition: all .1s linear;
  box-shadow: 0 0 8px #000;
}

